
import {
  showLoader,
  hideLoader
} from './js/loader'; // Import funkcji obsugujcych loader


import { fetchMovies } from './js/fetch';
import { drawMovies } from './js/draw-movie'; 
import * as modalMovie from './js/modal-movie';

import './sass/main.scss';

const form = document.querySelector('.search-form');
const searchField = document.querySelector('[name="searchQuery"]');
const moviesGallery = document.querySelector('.gallery__list');

let page = 1;

form.addEventListener('submit', function (event) {
  event.preventDefault();
  const inputValue = searchField.value;
  page = 1;

  showLoader(); // Poka loader przed rozpoczciem wyszukiwania filmw

  const movies = fetchMovies(inputValue);
  console.log(movies);
  moviesGallery.innerHTML = '';
  drawMovies(inputValue);

  hideLoader(); // Ukryj loader po zakoczeniu wyszukiwania filmw
});